@import "vars";
@import "fonts";
@import "libs";

::placeholder {
	color: $gray; }

html {
	font-size: 10px; }
body {
	font-size: 1.4rem;
	letter-spacing: 0.003rem;
	min-width: 320px;
	position: relative;
	line-height: 1.5;
	font-family: $default-font;
	overflow-x: hidden;
	color: $text;
	input, textarea, button, a {
		&:active, &:hover, &:focus {
			outline: 0;
			outline-offset: 0; } }
	input, textarea {
		border: none;
		background: transparent; }
	button {
		cursor: pointer;
		background-color: transparent;
		border: none;
		outline: none;
		padding: 0; } }
b, strong {
	font-weight: bold; }
.icon {
	display: inline-block;
	transition: 0.25s;
	&-left {
		margin-right: 1rem; }
	&-right {
		margin-left: 1rem; }
	&-logo {
		display: block;
		@include size(18rem, 2.7rem);
		&-mobile {
			@include size(14.5rem, 1.5rem); } }
	&-dropdown {
		@include size(.8rem, .5rem);
		&.icon-large {
			@include size(2.7rem, 1.5rem); } }
	&-phone {
		@include size(1.5rem, 1.5rem);
		fill: #fff; }
	&-search {
		@include size(1.6rem, 1.7rem); }
	&-arrow {
		@include size(5.1rem, 2.6rem);
		&.icon-large {
			@include size(7.1rem, 3.5rem); } }
	&-twitter {
		@include size(1.8rem, 2rem); }
	&-telegram {
		@include size(2rem, 2rem); }
	&-instagram {
		@include size(2rem, 2rem); }
	&-arrows {
		@include size(2.5rem, 4.5rem);
		stroke: #707070;
		&.icon-small {
			@include size(1.1rem, 1.9rem); }
		&.icon-large {
			@include size(3.3rem, 5.7rem); } }
	&-download {
		@include size(2rem, 2.1rem); }
	&-menu {
		@include size(1.6rem, 1.4rem); }
	&-close {
		@include size(1.4rem); }
	&-chat {
		@include size(2.6rem, 3.1rem); }
	&-up {
		@include size(2.6rem, 2rem); }
	&-wrapper {
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #ffffff;
		box-shadow: 0 0.3rem 4rem rgba(0, 0, 0, 0.1);
		&_big {
			border-radius: 2rem;
			@include size(16.8rem);
			img {
				display: block;
				max-width: 6.6rem;
				max-height: 6.6rem; } }
		&_small {
			border-radius: 1rem;
			@include size(6.8rem);
			img {
				display: block;
				max-width: 2.8rem;
				max-height: 2.8rem; } } }
	&-block {
		&_small {
			img {
				max-width: 4rem;
				height: auto; } }
		&_medium {
			height: 8rem;
			display: flex;
			align-items: center;
			justify-content: center;
			img {
				max-width: 8rem;
				height: auto; } } } }
img {
	@include img-fluid; }
.hidden {
	visibility: hidden;
	opacity: 0;
	pointer-events: none;
	position: absolute;
	bottom: 0;
	left: 0; }
a {
	color: #0099ff;
	text-decoration: none;
	&:hover {
		text-decoration: underline;
		color: #0099ff; } }
.list {
	&-unstyled {
		@include list-unstyled; }
	&-dropdown {
		position: absolute;
		background-color: #ffffff;
		box-shadow: 0 0.5rem 1.6rem rgba(0, 0, 0, 0.2);
		border-radius: 0.6rem;
		padding: 1.3rem 1rem;
		opacity: 0;
		visibility: hidden;
		pointer-events: none;
		z-index: 15;
		min-width: 215px;
		transition: 0.25s;
		&_item {
			white-space: nowrap;
			border-top: 0.1rem solid #e4e4e4;
			&:first-child {
				border-top: none; } }
		&_link {
			font-size: 1.4rem;
			display: block;
			color: #29293a;
			letter-spacing: 0.002rem;
			line-height: 4rem;
			padding: {
				left: .7rem;
				right: .7rem; }
			transition: 0.25s;
			&:hover {
				text-decoration: none;
				color: #29293a;
				transform: translateX(.7rem); } } } }
a.image:not(.products-block) {
	overflow: hidden;
	position: relative;
	transition: .4s;
	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		@include size(100%);
		background: inherit;
		transition: inherit; }
	&:hover {
		&:before {
			transform: scale(1.2); } } }
.image {
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	display: block;
	&-23 {
		padding-bottom: 62.5%; }
	&-28 {
		padding-bottom: 41.5%; }
	&-30 {
		padding-bottom: 111%; }
	&-36 {
		padding-bottom: 58.7%; }
	&-40 {
		padding-bottom: 70.5%; }
	&-50 {
		padding-bottom: 74%; }
	&-54 {
		padding-bottom: 54%; }
	&-63 {
		padding-bottom: 127%; }
	&-64 {
		padding-bottom: 64%; }
	&-top {
		border-radius: 1rem 1rem 0 0; }
	&-right {
		border-radius: 0 1rem 1rem 0; }
	&-left {
		border-radius: 1rem 0 0 1rem; }
	&-bottom {
		border-radius: 0 0 1rem 1rem; }
	&-both {
		border-radius: 1rem; } }
// typography
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
	display: block;
	margin-bottom: 0;
	line-height: 1.2;
	font-weight: 400; }
h1, .h1 {
	font-size: 4.8rem;
	font-weight: 700;
	letter-spacing: 0.01rem; }
h2, .h2 {
	font-size: 3.2rem;
	font-weight: 600;
	letter-spacing: 0.006rem; }
h3, .h3 {
	font-size: 2.4rem;
	letter-spacing: 0.005rem; }
h4, .h4 {
	font-size: 2.4rem;
	font-weight: 700;
	letter-spacing: 0.005rem; }
h5, .h5 {
	font-size: 2.1rem;
	font-weight: 600;
	letter-spacing: 0.004rem; }
h6, .h6 {
	font-size: 1.8rem;
	letter-spacing: 0.004rem; }
h7, .h7 {
	font-size: 1.8rem;
	font-weight: 600;
	letter-spacing: 0.004rem;
	line-height: 1.5;
	display: flex;
	align-items: center;
	min-height: 5.4rem;
	justify-content: center; }
blockquote {
	padding-left: 3.6rem;
	border-left: .3rem solid rgba(#c8c8c8, .2);
	font-size: 1.4rem; }
figure {
	margin: 2rem 0;
	img {
		@include img-fluid;
		margin-bottom: 1rem;
		width: 100%; }
	figcaption {
		font-size: 1.2rem;
		line-height: 1.5;
		font-style: italic; } }
.text {
	&-large {
		font-size: 1.8rem;
		letter-spacing: 0.004rem; }
	&-medium {
		font-size: 1.4rem;
		letter-spacing: 0.003rem; }
	&-small {
		font-size: 1.2rem;
		letter-spacing: 0.002rem;
		line-height: 1.33; }
	&-gray {
		color: #7f7f7f; }
	&-limited {
		max-width: 15rem; } }
.headline {
	display: block;
	font-size: 4.8rem;
	font-weight: 700;
	letter-spacing: 0.01rem;
	line-height: 1.25;
	&-inner {
		&:after {
			content: '';
			display: block;
			@include size(11.5rem, .3rem);
			background-image: linear-gradient(to right, #ffa31a 0%, #f73c71 100%);
			margin-top: 2.3rem; }
		&.text-center {
			&:after {
				margin: {
					left: auto;
					right: auto; } } } }
	&-big {
		font-size: 8rem; }
	&-small {
		&:after {
			content: '';
			display: block;
			@include size(4rem, .3rem);
			background-image: linear-gradient(to right, #ffa31a 0%, #f73c71 100%);
			margin-top: 1rem; } }
	&-medium {
		&:after {
			content: '';
			display: block;
			@include size(10rem, .1rem);
			background-image: linear-gradient(to right, #ffa31a 0%, #f73c71 100%);
			margin-top: 1rem; } }
	&-subtitle {
		display: block;
		margin-top: 2rem;
		font-size: 1.4rem;
		font-weight: 400; }
	&-main {
		font-size: 5.4rem;
		font-weight: 900;
		letter-spacing: 0.011rem;
		line-height: 1.2;
		font-family: $proxi-font!important; } }
.h1-i, .h2-i, .h3-i, .h4-i, .h5-i {
	font-family: $osw-font!important;
	font-weight: 400; }
.h1-i {
	font-size: 3.8rem; }
.h2-i {
	font-size: 3.2rem; }
a.h2-i {
	color: inherit;
	&:hover {
		text-decoration: none;
		opacity: 0.8; } }
.h3-i {
	font-size: 2.8rem; }
.h4-i {
	font-size: 2.4rem; }
.h5-i {
	font-size: 2.1rem; }
// typography

// btns
.btn {
	display: inline-block;
	cursor: pointer;
	transition: 0.25s;
	&:hover {
		text-decoration: none; }
	&-size {
		&_m {
			height: 4.8rem;
			line-height: 4.8rem;
			border-radius: 0.4rem;
			font-size: 1.2rem;
			font-weight: 700;
			text-transform: uppercase;
			padding: {
				left: 4rem;
				right: 4rem; }
			&.btn-color_bd {
				line-height: 4.4rem; } } }
	&-color {
		&_orange {
			background-color: $accent;
			color: $white;
			&:hover {
				color: $white;
				background-color: #ff8000; } }
		&_blue {
			background-color: #2699fb;
			color: $white;
			&:hover {
				background-color: #008aff;
				color: $white; } }
		&_bd {
			border-radius: 0.4rem;
			border: 0.2rem solid #ffffff;
			color: #ffffff;
			&:hover {
				color: $text;
				background-color: #ffffff; } }
		&_trans {
			border: 0.1rem solid #d4d4d4;
			color: $text;
			border-radius: 0.4rem;
			&:hover {
				color: $text;
				background-color: #d4d4d4; } } }
	&-link {
		&:hover {
			text-decoration: none; }
		.icon-arrow {
			stroke: #707070;
			transition: 0.25s; } } }
// btns

.overflow {
	overflow: hidden; }

// search
.search {
	padding: 0;
	width: 0;
	flex: 0;
	overflow: hidden;
	transition: 0.4s;
	&.is-show {
		width: 17rem;
		flex: 0 0 17rem;
		margin: {
			left: 1.6rem;
			right: 1.6rem; } }
	&-control {
		border-bottom: 1px solid $gray;
		width: 100%;
		padding: {
			right: 2rem;
			left: 1rem; } }
	&-btn {
		position: absolute;
		right: .5rem;
		top: 50%;
		transform: translateY(-50%);
		.icon-close {
			@include size(1rem); } } }
// search

// section
.section {
	margin-bottom: 4%;
	&-title {
		margin-bottom: 2.8rem;
		&-inner {
			margin-bottom: 3.9rem; } }
	&-white {
		background-color: #ffffff;
		box-shadow: 0 0.3rem 6rem rgba(0, 18, 33, 0.1);
		z-index: 2;
		padding: {
			top: 4.6%;
			bottom: 4.8%; } } }
.container {
	&-30 {
		> .row {
			margin: {
				left: -3.2rem;
				right: -3.2rem; }
			> * {
				padding: {
					left: 3.2rem;
					right: 3.2rem; } } } } }

// section

// carousel
.owl {
	&-carousel {
		position: relative; }
	&-nav {
		position: absolute;
		top: 50%;
		transform: translate(-50%, -50%);
		width: calc(100% + 12rem * 2);
		left: 50%;
		display: flex;
		justify-content: space-between;
		pointer-events: none; }
	&-next {
		transform: rotate(180deg); }
	&-prev, &-next {
		transition: 0.25s;
		pointer-events: auto;
		.icon-arrows {
			stroke: #dfdfdf;
			&:hover {
				stroke: #000; } }
		&.disabled {
			opacity: 0;
			visibility: hidden; } }
	&-dots {
		display: flex;
		position: absolute;
		@extend .container;
		left: 50%;
		transform: translateX(-50%);
		bottom: 3.2rem;
		z-index: 10; }
	&-dot {
		display: block;
		@include size(.8rem);
		background-color: #fff!important;
		opacity: 0.7;
		transition: 0.25s;
		border-radius: 100%;
		margin-left: 1rem;
		&:first-child {
			margin-left: 0; }
		&:hover, &.active {
			opacity: 1; }
		span {
			display: none; } }
	&-scrollbar {
		margin-top: 4rem;
		width: 100%;
		height: 2.2rem;
		border-radius: 0.4rem;
		border: 0.1rem solid rgba(112, 112, 112, 0.16);
		background-color: #ebebeb;
		position: relative;
		.owl-scroll {
			&-handle {
				width: 6.8rem;
				height: 2rem;
				border-radius: 0.4rem;
				border: 0.1rem solid rgba(112, 112, 112, 0.16);
				background: #ed6e28 url(../img/scrollbar.png) center no-repeat;
				display: block;
				position: absolute;
				top: 0;
				left: 0;
				&:hover {
					background-color: darken(#ed6e28, 3%); } }
			&-progress {
				display: none; } } }
	&-carousel {
		.animated {
			animation-duration: 1000ms;
			animation-fill-mode: both; }
		.owl-animated-in {
			z-index: 1; }
		.owl-animated-out {
			z-index: 1; }
		.anZoom {
  			animation-name: anZoom; } } }
@keyframes anZoom {
	0% {
    	opacity: 1; }
	100% {
		opacity: 0; } }
.promo-inner {
	.owl {
		&-dots {
			justify-content: center;
			bottom: 2rem; }
		&-dot {
			@include size(1.5rem);
			margin-left: 2rem; } } }
// carousel

// projects
.projects {
	p:last-child {
		margin-bottom: 0!important; } }
// projects

// items
.items {
	.card-block {
		padding: 3rem 6.2rem;
		color: $text;
		.headline-small:after {
			transition: 0.25s; }
		&:hover {
			color: $text;
			.headline-small:after {
				width: 10rem; } }
		.col {
			padding-left: 4.2rem;
			p {
				max-width: 57.3rem; } } }
	.icon-arrows {
		transform: rotate(180deg);
		stroke: #dfdfdf; } }
// items

// header
.header {
	&-mobile {
		box-shadow: 0 0.3rem 1rem rgba(0, 0, 0, 0.05);
		z-index: 3;
		position: relative;
		.container > .row {
			height: 6rem; }
		&_close, &_input {
			display: none; }
		&_input {
			input {
				width: 100%;
				border-bottom: .1rem solid $gray; } } }
	&-topbar {
		background-color: $text;
		color: $white;
		font-size: 1.4rem;
		.container > .row {
			height: 3.8rem; }
		&_list {}
		&_item {}
		&_link {
			.icon-dropdown {
				stroke: $white; } } }
	&-topbar, &-phone {
		&_link {
			color: $white;
			&:hover {
				color: $white; } } }
	&-phone {
		&_link {
			&:hover {
				text-decoration: none; } } }
	&-navbar {
		box-shadow: 0 0.3rem 0.6rem rgba(0, 0, 0, 0.16);
		background-color: #ffffff;
		z-index: 2;
		position: relative;
		font-size: 1.8rem;
		.container > .row {
			height: 7rem; }
		&_list {}
		&_item {}
		&_link {
			color: $black;
			&:hover {
				color: $black; }
			.icon-dropdown {
				stroke: $black; } } }
	&-topbar, &-navbar {
		&_link {
			&.is-show {
				.icon-dropdown {
					transform: rotate(180deg); } } }
		&_item:hover {
			.icon-dropdown {
				transform: rotate(180deg); }
			.list-dropdown {
				opacity: 1;
				visibility: visible;
				pointer-events: auto; } } } }
// header

// not-found
.not-found {
	padding: {
		top: 5rem;
		bottom: 5rem; }
	min-height: 100vh; }
.main {
	.not-found {
		min-height: calc(100vh - 10.8rem); } }
// not-found

// info
.info {
	&-text {
		p {
			margin-bottom: 5rem; } }
	&-inner {
		.info-text p {
			margin-bottom: 4.5rem;
			line-height: 1.56; } }
	&-content {
		@extend .text-large;
		p, blockquote {
			margin-bottom: 2.5rem;
			line-height: 1.56; }

		&_column {
			column-count: 2;
			column-gap: 13.4rem; } }
	&-share {
		margin-top: 4.5rem;
		position: relative;
		padding-top: 4.3rem;
		&:before {
			content: '';
			@include size(11.5rem, .3rem);
			background-color: #d8d8d8;
			position: absolute;
			top: 0;
			left: 50%;
			display: block;
			transform: translateX(-50%); }
		&_item {
			display: block;
			padding: .9rem;
			border-radius: 0.4rem!important;
			transition: 0.25s;
			&:hover {
				transform: translateY(-.5rem); } } } }
// info

// promo
.promo {
	position: relative;
	&-content {
		min-height: 41rem;
		padding: {
			top: 5rem;
			bottom: 5rem; } }
	&-block {
		background-size: cover;
		background-position: center;
		height: 100%; }
	&-carousel {
		&_block {
			padding: 2.6rem 1.9rem 1.4rem; } }
	&-substrate_carousel {
		position: absolute;
		top: 0;
		left: 0;
		@include size(100%);
		.owl-stage-outer, .owl-stage-outer * {
			height: 100%; } }
	&-text {
		color: $white;
		background-color: rgba(#000 ,.8);
		position: relative;
		z-index: 10;
		&_title {
			display: block;
			margin-bottom: 2.5rem; }
		p {
			margin-bottom: 2.7rem; } }
	&-services, &-text {
		&_link {
			color: $white;
			font-size: 1.8rem;
			&:hover {
				color: $white; }
			.icon {
				&-right {
					margin-left: 4.2rem; }
				&-arrow {
					stroke: $white; } } } }
	&-services {
		color: $white;
		overflow: hidden;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		> * {
			transform: translateY(3.5rem);
			transition: 0.25s; }
		&:hover {
			color: $white;
			text-decoration: none;
			> * {
				transform: translateY(0); }
			.promo-services_link, &:after {
				opacity: 1;
				visibility: visible; }
			&:after {
				top: -8rem; } }
		padding: {
			left: 3.5rem;
			right: 3.5rem; }
		&:after {
			content: '';
			position: absolute;
			@include size(47.5rem, 8rem);
			display: block;
			top: 0;
			left: 50%;
			visibility: hidden;
			opacity: 0;
			transform: translateX(-50%);
			z-index: 2;
			pointer-events: none;
			transition: 0.25s; }
		&.bgc {
			&-orange {
				&:after {
					background: url(../img/services-orange.png) center no-repeat; } }
			&-blue {
				&:after {
					background: url(../img/services-blue.png) center no-repeat; } } }
		&_title {
			display: block;
			margin-bottom: 2.2rem; }
		p {
			margin-bottom: 0; }
		&_link {
			margin-top: 3.4rem;
			opacity: 0;
			visibility: hidden;
			text-align: left; } }
	&-inner {
		position: relative;
		&:before {
			content: '';
			display: block;
			@include size(50%,100%);
			position: absolute;
			top: 0;
			left: 0;
			background-image: linear-gradient(89deg, rgba(0, 0, 0, 0.79) 0%, rgba(0, 0, 0, 0.77) 39%, rgba(0, 0, 0, 0) 100%);
			opacity: 0.8;
			pointer-events: none;
			z-index: 2; }
		+ .section-white {
			padding: {
				top: 0;
				bottom: 2%; } }
		&_content {
			min-height: 30rem;
			padding: {
				top: 3rem;
				bottom: 3rem; } }
		&_title {
			margin-bottom: 1.4rem; }
		&_text {
			position: relative;
			z-index: 3;
			color: $white;
			p {
				margin-bottom: 2.6rem; } }
		&_link {
			color: $white;
			font-size: 1.2rem;
			font-weight: 700;
			text-transform: uppercase;
			&:hover {
				color: $white; } } } }
// promo

// services
.services {
	background: linear-gradient(90deg, rgb(38, 153, 251) 50%, rgb(255, 141, 13) 50%);
	position: relative;
	&:before, &:after {
		content: '';
		position: absolute;
		display: block;
		top: 50%;
		transform: translateY(-50%);
		background-repeat: no-repeat; }
	&:before {
		left: 0;
		@include size(17.3rem, 22.3rem);
		background-image: url(../img/promo-img_01.png);
		background-position: right center; }
	&:after {
		right: 0;
		@include size(16.9rem, 24.9rem);
		background-image: url(../img/promo-img_02.png);
		background-position: left center; }
	.container > .row {
		height: 25rem; } }
