// brands
// brands

// breadcrumb
.breadcrumb {
	padding: {
		top: 1%;
		bottom: .5%; }
	margin-bottom: 2.3rem;
	&-list {}
	&-item {
		display: flex;
		align-items: center;
		&:before {
			content: '';
			display: block;
			@include size(13px, 8px);
			background-image: url(../img/icon/icon-bread.svg);
			margin: {
				left: 1.1rem;
				right: .9rem; } }
		&:first-child {
			&:before {
				display: none; } } }
	&-link {
		color: #29293a;
		&:hover {
			color: #29293a; } } }
// breadcrumb

// faq
.faq {
	overflow: hidden;
	&-box {}
	&-block {
		border-top: 1px solid #ededed;
		cursor: pointer;
		&:last-child {
			border-bottom: 1px solid #ededed; }
		> * {
			padding: {
				left: 4rem;
				right: 9.9rem; } }
		&.is-active {
			.faq-block_title:before {
				opacity: 0;
				visibility: hidden; } }
		&_title {
			display: block;
			position: relative;
			padding: {
				top: 1.2rem;
				bottom: 1.2rem; }
			&:hover {
				&:before, &:after {
					background-color: darken(#e6e6e6, 10%); } }
			&:before, &:after {
				content: '';
				display: block;
				position: absolute;
				background-color: #e6e6e6;
				top: 50%;
				transform: translateY(-50%);
				transition: 0.25s; }
			&:before {
				@include size(.4rem, 1.9rem);
				right: 3.7rem; }
			&:after {
				@include size(1.9rem, .4rem);
				right: 3rem; } }
		&_content {
			display: none;
			padding: {
				top: 1.4rem;
				bottom: 1.4rem; }
			p {
				margin-bottom: 0!important; } } }
	.card-block {
		padding: 4rem 10rem 1rem 3rem;
		.faq {
			&-box {
				padding-left: 7rem; }
			&-block {
				> * {
					padding-left: 0; }
				&:first-child {
					border-top: none; }
				&:last-child {
					border-bottom: none; }
				&_title {
					&:after {
						right: 0; }
					&:before {
						right: 0.7rem; } } } } } }
// faq

// documents
.documents {
	overflow: hidden;
	&-box {}
	&-block {
		padding: 1.5rem 2rem 1rem;
		border-top: 1px solid #ededed;
		color: $text;
		display: block;
		&:last-child {
			border-bottom: 1px solid #ededed; }
		&:hover {
			text-decoration: none;
			color: $text;
			.documents-block_name {
				transform: translateX(.5rem); } }
		&_name {
			transition: 0.25s; } }
	.card-block {
		padding: 4rem 10rem; } }
// documents

// compare
.compare {
	&-items {
		padding-top: 9.4rem;
		flex: 0 0 35rem;
		width: 35rem; }
	&-item {
		height: 9.5rem;
		display: flex;
		flex-direction: column;
		justify-content: center;
		position: relative;
		padding-left: 2rem;
		&:before, &:last-child:after {
			content: '';
			display: block;
			position: absolute;
			background-color: rgba(#707070,.1);
			@include size(100%,.1rem);
			left: 0; }
		&:before {
			top: 0; }
		&:last-child:after {
			bottom: -.1rem; } }
	&-carousel {
		flex: 0 0 calc(100% - 35rem);
		max-width: calc(100% - 35rem); }
	&-box {
		background-color: #ffffff;
		border-radius: 0.6rem; }
	&-block {
		height: 9.5rem;
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: center;
		position: relative;
		flex-direction: column;
		&:after {
			content: '';
			display: block;
			position: absolute;
			background-color: rgba(#707070,.1);
			@include size(calc(100% + 2rem),.1rem);
			left: -2rem;
			bottom: 0; }
		&:last-child {
			height: auto;
			padding: {
				top: 3rem;
				bottom: 2rem; }
			&:after {
				display: none; } }
		.h5 {
			padding: {
				left: 1.5rem;
				right: 1.5rem; } } }
	&-price {
		align-items: center;
		margin-bottom: 2rem;
		&_num {
			color: $accent;
			margin-bottom: .8rem; }
		&_text {
			display: block;
			text-align: center; } }
	&-no, &-yes {
		display: block;
		@include size(4.6rem, 3.6rem); }
	&-no {
		background: #fff url(../img/compare-no.svg) center no-repeat; }
	&-yes {
		background: #fff url(../img/compare-yes.svg) center no-repeat; } }
