	&-carousel {
 } }		// width: 60.5rem
.slick-slider {
	margin-left: -3.5%;
	margin-right: -3.5%; }
.slick-list {
	padding-top: 10% !important;
	padding-bottom: 10% !important;
	padding-left: 15% !important;
	padding-right: 15% !important; }
.slick-dots {
	text-align: right;
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0; }
.slick-track {
	max-width: 100% !important; }
.slick-slide {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	margin: auto;
	opacity: 0;
	transform: translate3d(0, 0, 0);
	transition: transform 1s, opacity 1s; }
.slick-snext, .slick-sprev {
	display: block; }
.slick-current {
	opacity: 1;
	position: relative;
	display: block;
	z-index: 2; }
.slick-snext {
	opacity: 1;
	transform: translate3d(30%, 0, -20px);
	z-index: 1; }
.slick-sprev {
	opacity: 1;
	transform: translate3d(-30%, 0, -20px); }
