// products
.products {
	&-box {
		padding: {
			top: 2.6%; }
		> *:first-child {
			height: 60rem;
			text-align: center; }
		.row {
			&:first-child {
				height: 27.2rem; }
			&:last-child {
				height: 32.8rem; }
			> * {
				height: 100%; } } }
	&-block {
		display: flex;
		align-items: flex-end;
		background-position: center;
		background-size: cover;
		position: relative;
		&:after {
			content: '';
			display: block;
			@include size(100%);
			background-image: linear-gradient(to top, rgba(0, 0, 0, 0.68) 0%, rgba(0, 0, 0, 0) 100%);
			opacity: 0.3;
			position: absolute;
			right: 0;
			left: 0;
			pointer-events: none;
			z-index: 0; }
		&:hover {
			text-decoration: none; }
		&_small {
