						@include size(13.4rem, 7rem); } } }
			&-blue {
				.products {
					&-category {
						background-color: $aqua; }
					&-content_link {
						background: url(../img/products-blue.png) center no-repeat; } }
				&.products-block_small {
					.products-content_link {
