		border-radius: 0.4rem; }
	&-content {
		color: $white;
		padding: 2.3rem 2.4rem 0;
		position: relative;
		z-index: 1;
		overflow: hidden;
		> * {
			transform: translateY(11.1rem);
			transition: 0.25s; }
		&_title {
			margin-bottom: 2.2rem; }
		p {
			margin-bottom: 0; }
		&_link {
			margin: 3rem auto 0;
			color: #ffffff;
			opacity: 0;
			visibility: hidden;
			display: flex;
