	&-group {
		position: relative; }
	&-true, &-false {
		.form-control {
			padding-right: 3rem; }
		&:after {
			display: block;
			text-align: center;
			font-size: 1.4rem;
			position: absolute;
			top: calc(100% + 1rem);
			width: 100%; }
		&:before {
			content: '';
			display: block;
			right: 1rem;
			top: 50%;
			transform: translateY(-50%);
			position: absolute; } }
	&-true {
		.form-control {
			border: 0.1rem solid #4dc213!important; }
		&:after {
			content: 'Подключение доступно';
			color: #4dc213; }
		&:before {
			@include size(1.8rem, 1.2rem);
			background: url(../img/form-true.svg) center no-repeat; } }
	&-false {
		.form-control {
			border: 0.1rem solid #f64622!important; }
		&:after {
			content: 'Подключение НЕ доступно';
