		&:before {
			@include size(1.6rem);
			background: url(../img/form-false.svg) center no-repeat; } }
	&-label {
		display: block;
		font-size: 2.1rem;
		font-weight: 600;
		min-height: 3.1rem; }
	&-control {
		display: block;
		width: 100%;
		border: 0.1rem solid $gray;
		border-radius: 0.4rem;
		background-color: #ffffff;
		transition: 0.25s;
		padding: {
			left: 1.6rem;
			right: 1.6rem; }
		&:focus {
			border-color: $accent; }
		&[type=text], &[type=email] {
			height: 4.8rem;
			line-height: 4.8rem; }
		&[type=file] {
			opacity: 0;
			visibility: hidden;
			position: absolute;
			pointer-events: none;
			width: 1px!important; }
		&[name=message] {
			height: 14rem;
			resize: none;
			padding: {
				top: 1.5rem;
				bottom: 1.5rem; } } }
	&-checkbox {
		position: absolute;
		top: 0;
		left: 0;
		visibility: hidden;
		opacity: 0;
		+ .form-label {
			position: relative;
			padding-left: 4.5rem;
			cursor: pointer;
			font-size: 1.4rem;
			min-height: 3rem!important;
			display: flex;
			flex-direction: column;
			justify-content: center;
			color: #1e1f2d;
			font-weight: 400;
			&:after, &:before {
				content: '';
				position: absolute;
				display: block;
				border-radius: 0.6rem;
				transition: 0.25s; }
			&:before {
				@include size(3rem);
				left: 0;
				top: 0;
				background-color: #ffffff;
				border: 0.1rem solid #bec3d0; }
